<template>
  <el-table
      :data="tableData"
      height="689px"
      border
      style="width: 100%"
      :header-cell-style="{background:'#ffd04b',color:'#606266'}"
      :row-class-name="shop">
    <el-table-column
        align="center"
        prop="id"
        label="订单编号"
        width="180">
    </el-table-column>
    <el-table-column
        align="center"
        prop="room_id"
        label="包间编号"
        width="180">
    </el-table-column>
    <el-table-column
        align="center"
        prop="cost"
        label="饮料消费"
        width="180">
    </el-table-column>
    <el-table-column
        align="center"
        prop="drinks"
        label="消费明细">
    </el-table-column>
  </el-table>
</template>

<script>
export default {
  name: "drinks",
  methods:{
    shop({row,rowIndex}){
      if(rowIndex%2 ===0){
        return 'success-row'
      }
      return ''
    }
  },
  data() {
    return {
      tableData: []
    }
  }
}
</script>
<style >
.el-table .success-row {
  background: #f0f9eb;
}
</style>


